/* src/app/keyboard/keyboard.scss */
.correct {
  background-color: #52c41a;
  border-color: #52c41a;
  color: white;
}
.incorrect {
  background-color: #ff4d4f;
  border-color: #ff4d4f;
  color: white;
}
.hint {
  background-color: #faad14;
  border-color: #faad14;
  color: white;
}
.keyboard {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.keyboard-row {
  display: flex;
  justify-content: center;
  gap: 4px;
}
@media (max-width: 400px) {
  .keyboard-row {
    gap: 2px;
  }
}
.keyboard-row button {
  height: 50px;
  min-width: 20px;
  flex: 1;
  max-width: 45px;
  font-size: clamp(10px, 4vw, 16px);
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
}
@media (max-width: 767px) {
  .keyboard-row button {
    height: 55px;
    font-size: clamp(12px, 5vw, 18px);
  }
}
@media (max-width: 400px) {
  .keyboard-row button {
    height: 50px;
    font-size: clamp(8px, 3.5vw, 14px);
    min-width: 25px;
    max-width: 35px;
  }
}
@media (max-width: 320px) {
  .keyboard-row button {
    font-size: clamp(6px, 3vw, 12px);
    min-width: 20px;
    max-width: 30px;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .keyboard-row button {
    height: 52px;
    font-size: clamp(14px, 4.5vw, 17px);
  }
}
.keyboard-row:last-child button:first-child,
.keyboard-row:last-child button:last-child {
  flex: 2;
  min-width: 45px;
  max-width: 80px;
  font-weight: normal;
}
